<template>
  <div class="index">
    <transition name="bounce">
      <Header v-show="showHeader"></Header>
    </transition>
    <div class="container">
      <div class="home row l-r">
        <LeftBox
          v-if="isShowLeft&&baseData.isNotPhotoClick"
          @changeLeftShow="changeLeftShow"
        ></LeftBox>
        <router-view />
      </div>
    </div>
    <footer>
      <p>
        Design by
        <a href="/">一世风尘梦离骚</a>
      </p>
    </footer>
    <!-- <a
      href="#"
      class="cd-top cd-is-visible"
    >Top</a> -->
    <Back-top></Back-top>
  </div>
</template>

<script>
import Header from "@/components/common/header/header";
import LeftBox from "@/components/common/left_box/leftBox";
export default {
  components: {
    Header,
    LeftBox
  },
  computed: {
    // modulePermission() {
    //   return this.$store.getters.getModulePermission;
    // },
  },
  watch: {
    // modulePermission: {
    //   handler: function (newName, oldName) {
    //     return newName;
    //   },
    //   immediate: true,
    // },
  },
  data() {
    return {
      scrollTop: "",
      showHeader: true,
      i: 0,
      isShowLeft: true
    };
  },
  methods: {
    changeLeftShow() {
      if (window.screen.width < 414) {
        this.baseData.isNotPhotoClick = false;
      } else {
        this.baseData.isNotPhotoClick = true;
      }
    },
    //滚动条在Y轴上的滚动距离
    getScrollTop() {
      var scrollTop = 0,
        bodyScrollTop = 0,
        documentScrollTop = 0;
      if (document.body) {
        bodyScrollTop = document.body.scrollTop;
      }
      if (document.documentElement) {
        documentScrollTop = document.documentElement.scrollTop;
      }
      scrollTop =
        bodyScrollTop - documentScrollTop > 0
          ? bodyScrollTop
          : documentScrollTop;
      return scrollTop;
    },
    //文档的总高度
    getScrollHeight() {
      var scrollHeight = 0,
        bodyScrollHeight = 0,
        documentScrollHeight = 0;
      if (document.body) {
        bodyScrollHeight = document.body.scrollHeight;
      }
      if (document.documentElement) {
        documentScrollHeight = document.documentElement.scrollHeight;
      }
      scrollHeight =
        bodyScrollHeight - documentScrollHeight > 0
          ? bodyScrollHeight
          : documentScrollHeight;
      return scrollHeight;
    },
    //浏览器视口的高度
    getWindowHeight() {
      var windowHeight = 0;
      if (document.compatMode == "CSS1Compat") {
        windowHeight = document.documentElement.clientHeight;
      } else {
        windowHeight = document.body.clientHeight;
      }
      return windowHeight;
    },
    handleScroll() {
      //判断兼容性**很重要***
      this.scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // var scrollTop =
      //   window.pageYOffset ||
      //   document.documentElement.scrollTop ||
      //   document.body.scrollTop;
      if (this.scrollTop <= 62) {
        this.showHeader = true;
        $(".cd-top").hide();
      } else {
        this.showHeader = false;
      }
      let scroll = this.scrollTop - this.i;
      this.i = this.scrollTop;

      if (scroll < 0) {
        // console.log("up");
      } else {
        // console.log("down");
      }
      if (
        this.getScrollTop() + this.getWindowHeight() ==
        this.getScrollHeight()
      ) {
        $(".cd-top").show();
      }
    },
    //获取搞笑段子列视频表
    getJokeList() {
      const params = {
        count: 10,
        page: 1,
        type: "video"
      };
      this.$api
        .getJokeList(params)
        .then(response => {
          const result = response.data;
          if (result.code == "200") {
            //  this.$Message.success('获取数据成功');
          } else {
            this.$Message.error(result.message);
          }
        })
        .catch(error => {
          this.$Message.error("请求错误");
        });
    },
    //获取搞笑段子列表
    getJokeByTextList() {
      const params = {
        return_data: 0,
        num: 100,
        app_key: "0D01C4CA26367BA195F8402DACC184C7",
        sign: "CDF64287311F947362FC7C69AF1F9C21"
      };
      this.$api
        .getJokeByTextList(params)
        .then(response => {
          const result = response.data;
          if (result.ret == "200") {
            //  this.$Message.success('获取数据成功');
            let myData = [];
            let i = 3;
            result.data.joke[0].forEach((element, index) => {
              i += 1;
              myData.push({
                articleId: i,
                articleTitle: `笑话第" + ${index + 1} + "条`,
                src:
                  "http://www.yangqq.com/d/file/news/life/2018-04-27/762f99f369ae786f970477feeb3b9d77.jpg",
                isTop: false,
                subTitle: "笑话第" + index + "条",
                details: `<p class='paragraph'> ${element}  </p>`
              });
            });
            this.baseData.articleData = this.baseData.articleData.concat(
              myData
            );
            // console.log(this.baseData.articleData)
          } else {
            this.$Message.error(result.message);
          }
        })
        .catch(error => {
          this.$Message.error("请求错误");
        });
    },
    //获取上传的图片列表
    getUploadRecordListList() {
      const params = {
        return_data: 0,
        order_type: 1,
        page: 1,
        perpage: 1000,
        app_key: "0D01C4CA26367BA195F8402DACC184C7",
        sign: "CDF64287311F947362FC7C69AF1F9C21"
      };
      this.$api
        .getUploadRecordListList(params)
        .then(response => {
          const result = response.data;
          if (result.ret == "200") {
            //  this.$Message.success('获取数据成功');
            // console.log(result.data.list);
          } else {
            this.$Message.error(result.message);
          }
        })
        .catch(error => {
          this.$Message.error("请求错误");
        });
    }
  },
  updated() {
    let url = window.localStorage.getItem("url");
    if (window.screen.width < 414) {
      this.baseData.navItem.forEach(element => {
        if (element.url == url) {
          this.isShowLeft = element.min_modulePermission;
        }
      });
    } else {
      this.baseData.navItem.forEach(element => {
        if (element.url == url) {
          this.isShowLeft = element.max_modulePermission;
        }
      });
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
    $(".cd-top").hide();
    this.getJokeList();
    setTimeout(() => {
      this.getJokeByTextList();
      this.getUploadRecordListList();
    }, 400);
  },
  destroyed: function() {
    window.removeEventListener("scroll", this.handleScroll, true); //  离开页面清除（移除）滚轮滚动事件
  }
};
</script>

<style lang="scss" src="@/assets/scss/index.scss">
</style>


